<template><section><h2>Search 搜索框</h2>
<h3>基本用法</h3>
<p>search 事件在用户点击键盘上的 搜索/回车 按钮触发。</p>
<ul>
<li>Tips: 在 van-search 外层增加 form 标签，并且 action 不为空，即可在 IOS 弹出的输入法中显示搜索按钮</li>
</ul>
<div class="demo-block mt_15">
<form action="/">
  <tj-search v-model="searchName" @keyup.enter.native="search"></tj-search>
</form>

</div>
<demo-block>
                    <div slot="desc"><form action="/">
  <tj-search v-model="searchName" @keyup.enter.native="search"></tj-search>
</form>


</div>
                  <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">form</span> <span class="hljs-attr">action</span>=<span class="hljs-string">"/"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">tj-search</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"searchName"</span> <span class="hljs-attr">@keyup.enter.native</span>=<span class="hljs-string">"search"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">tj-search</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">form</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">searchName</span>: <span class="hljs-string">""</span>
    }
  },
  <span class="hljs-attr">methods</span>: {
    search() {
      <span class="hljs-keyword">this</span>.$toast(<span class="hljs-keyword">this</span>.searchName);
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>v-model</td>
<td>搜索结果绑定值</td>
<td>string</td>
<td></td>
<td></td>
</tr>
<tr>
<td>cancel-text</td>
<td>取消按钮文字</td>
<td>string</td>
<td></td>
<td></td>
</tr>
<tr>
<td>placeholder</td>
<td>搜索框占位内容</td>
<td>string</td>
<td></td>
<td>搜索</td>
</tr>
<tr>
<td>autofocus</td>
<td>自动聚焦</td>
<td>boolean</td>
<td></td>
<td>false</td>
</tr>
</tbody>
</table>
<h3>Events</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>cancel</td>
<td>点击取消按钮触发</td>
<td></td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
  data() {
    return {
      searchName: ""
    }
  },
  methods: {
    search() {
      this.$toast(this.searchName);
    }
  }
}
</script>


